	module.exports = "\n/**\n * That markdown on the left doesn't look great. Let's render it.\n */\n\n#work-text.flipped {\n  -webkit-transform: rotateX(0deg) rotateY(190deg) rotateZ(180deg);\n}\n\n#work-text .md {\n  -webkit-transform: rotateY(190deg) rotateZ(180deg);\n  margin-top: 800px;\n}\n\n/**\n * Alright. We're ready.\n *\n * 3...\n * 2...\n * 1...\n *\n * .\n * ....faked you out.\n *\n * Okay here we go!\n *\n */\n"

